/*************************
REGISTRATION FORM SECTION
**************************/
.form {
  border: 0.12em dashed #888;
  padding: 2em 1.5em;
}
.form--main {
  width: 90%;
  max-width: 640px;
  margin: 8em auto 3em;
  background-color: var(--color-white);
  font-family: "Poppins", sans-serif;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.8);
  padding: 0.45em;
}
.form legend {
  display: block;
  text-align: center;
  margin-bottom: 2em;
  font-size: 1.3em;
  text-transform: uppercase;
  font-weight: 700;
  font-family: "Nunito", sans-serif;
  color: var(--general-color);
  letter-spacing: 0.1em;
  position: relative;
}
.form legend::after, .form legend::before {
  content: "";
  position: absolute;
  bottom: 0;
  width: 30%;
  height: 0.2em;
  border-radius: 50%;
}
.form legend::after {
  right: 0;
  left: 60%;
  background-image: linear-gradient(to top right, var(--grad-color-1) 30%, var(--grad-color-2) 50%, var(--grad-color-3));
}
.form legend::before {
  left: 10%;
  background-image: linear-gradient(to top left, var(--grad-color-1) 30%, var(--grad-color-2) 50%, var(--grad-color-3));
}
.form__group {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  margin-bottom: 1em;
}
.form__group label {
  text-transform: capitalize;
  color: var(--label-color);
  letter-spacing: 0.03em;
  font-size: 1.1em;
}
.form__group textarea {
  width: 100%;
  padding: 0.6em 0.7em;
  font-family: "Noto Sans", sans-serif;
  font-size: 1em;
  letter-spacing: 0.75px;
  font-weight: 400;
  background-color: var(--input-color);
  border-radius: 0.25em;
  border: none;
  border: 0.1em solid var(--input-color);
  box-shadow: 0 0 3px rgba(0, 0, 0, 0.4);
  outline: none;
  resize: vertical;
  height: 15em;
}
.form__group textarea:focus {
  border-color: var(--input-border-color);
}
.form__group span {
  font-size: 0.7em;
  letter-spacing: 0.02em;
  color: #ce2727;
}
.form__submit__group {
  margin-bottom: 1em;
}
.form__submit__group input[type=submit] {
  margin-top: 1em;
  text-transform: uppercase;
  background-image: linear-gradient(to top, var(--btn-color-1), var(--btn-color-2));
  color: var(--color-white);
  width: 100%;
  padding: 0.75em;
  font-family: inherit;
  font-weight: 500;
  border: none;
  font-size: 1em;
  position: relative;
  border-radius: 0.35em;
  letter-spacing: 0.05em;
  box-shadow: 0 0 4px var(--btn-shadow-color);
}
.form__submit__group input[type=submit]:hover, .form__submit__group input[type=submit]:focus {
  cursor: pointer;
  background-image: linear-gradient(to top, var(--btn-hover-color-1), var(--btn-hover-color-2));
}
.form ::placeholder {
  font-style: italic;
}

/******************
Upload Button Styling
*********************/
input {
  width: 100%;
  font-family: "Nunito", sans-serif;
  font-weight: 500;
  font-size: 1em;
  border: 1px solid var(--input-border-color);
  border-radius: 0.4em;
}

.form__group input::file-selector-button {
  padding: 0.7em 2.5em;
  font-family: inherit;
  font-size: 1em;
  margin-right: 1em;
  background-image: linear-gradient(60deg, #29323c 0%, #485563 100%);
  color: var(--color-white);
  border: none;
  transition: background-color 300ms ease-out;
}
.form__group input::file-selector-button:hover, .form__group input::file-selector-button:focus {
  cursor: pointer;
  background-image: linear-gradient(60deg, #2f353b 0%, #223a53 100%);
}

.form__group input::-ms-browse {
  padding: 0.7em 2.5em;
  font-family: inherit;
  font-size: 1em;
  margin-right: 1em;
  background-image: linear-gradient(60deg, #29323c 0%, #485563 100%);
  color: var(--color-white);
  border: none;
  transition: background-color 300ms ease-out;
}
.form__group input::-ms-browse:hover, .form__group input::-ms-browse:focus {
  cursor: pointer;
  background-image: linear-gradient(60deg, #2f353b 0%, #223a53 100%);
}

.form__group input::-webkit-file-upload-button {
  padding: 0.7em 2.5em;
  font-family: inherit;
  font-size: 1em;
  margin-right: 1em;
  background-image: linear-gradient(60deg, #29323c 0%, #485563 100%);
  color: var(--color-white);
  border: none;
  transition: background-color 300ms ease-out;
}
.form__group input::-webkit-file-upload-button:hover, .form__group input::-webkit-file-upload-button:focus {
  cursor: pointer;
  background-image: linear-gradient(60deg, #2f353b 0%, #223a53 100%);
}